import React, { Component } from 'react'
import { imageConverter } from '../../../../helper/imageConverter'
export default class ImageTabDetails extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...this.props
        }
    }
    componentDidMount() {
        console.log(this.state);
    }

    onChangeFile = (e) => {
        const name = e.target.name
        imageConverter(e.target.files[0], name).then(res => {
            let documents = this.state.documents;
            documents.push(res)
            this.setState({ documents: documents });
        }).catch((err) => {
            console.error(err)
        })
        this.props.getImages({ ...this.state })

    }
    getImages = (data) => {
        this.props.getImages({ ...this.state })
        this.setState({ documents: data })
    }
    setData = () => {
        this.props.getImages({ ...this.state })
    }
    render() {
        const { documents } = this.state
        return (
            <>
                <div className="d-flex align-items-center justify-content-between mx-2">
                    <h6 className="tx-12  mg-b-0">Documents</h6>
                    <button className="btn btn-link" type="button" onClick={this.setData}>Save</button>
                </div>
                <div className="card-columns">
                    {
                        documents && documents.map((image, key) => {
                            if (Object.keys(image).length !== 0) {
                                return (
                                    <div key={key} className="card">
                                        <img src={image.image} className="card-img-top" alt="responsive" height={180} width={250} />
                                        <div className="card-footer">
                                            <h6 className="tx-uppercase ">{image.image_name}</h6>
                                        </div>
                                    </div>
                                )
                            }
                        })
                    }
                </div>

            </>
        )
    }
}
