import React, { Component } from 'react'

export default class RestaurantImageView extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...this.props
        }
    }

    componentDidMount() {
        console.log(this.props.documents)
    }
    render() {
        const { documents } = this.state
        return (
            <>
                <div className="card-columns">
                    {
                        documents && documents.map((image, key) => {
                            if (Object.keys(image).length !== 0) {
                                return (
                                    <div key={key} className="card">
                                        <img src={image.image} className="card-img-top" alt="responsive" height={180} width={250} />
                                        <div class="card-footer">
                                            <h6 class="tx-uppercase ">{image.image_name}</h6>
                                        </div>
                                    </div>
                                )
                            }
                        })
                    }
                </div>

            </>
        )
    }
}
