import React, { Component } from "react";
import AddRestaurantForm from "../components/restaurant/update/AddRestaurantForm"
import SideNav from "../components/SideNav";
import TopNav from "../components/TopNav";
import axios from 'axios'

export default class EditRestaurant extends Component {
    state = {}
    componentDidMount() {
        console.log('Loaded');
        let url = window.location.href;
        const idofclient = url.split('id=')[1];
        axios.get('/api/newrest/' + idofclient).then(res => {
            this.setState({ restaurant: res.data, isupdate: true })
        }).catch(err => console.error(err))
    }
    render() {
        const { restaurant, isupdate } = this.state
        return (
            <>
                <TopNav />
                <div className="contact-wrapper">
                    <SideNav />
                    {
                        isupdate ? <AddRestaurantForm {...restaurant} /> : <div />

                    }
                </div>
            </>
        );
    }
}
