import { Link } from "@react-pdf/renderer";
import axios from "axios";
import React, { Component } from "react";
import SideNav from "../components/SideNav";
import TopNav from "../components/TopNav";

export default class RequestScreen extends Component {
    state = {
        requests: [],
        current_restaurant: {}
    }
    componentDidMount() {
        axios.get('/api/partnerrequest').then(res => {
            let requests = []
            let obj = res.data.find(o => o.status === 'pending');
            requests.concat(obj)
            this.setState({ requests: res.data })
        }).catch(err => {
            console.log(err)
        })
        let url = window.location.href;
        let id = url.split('?')[1];
        console.log(typeof id);
        axios.get('/api/partnerrequest/' + id).then(res => {
            this.setState({ current_restaurant: res.data, req_id: id })
        }).catch(err => {
            console.log(err);
        })
    }
    render() {
        const { requests, req_id, current_restaurant } = this.state
        return (
            <>
                <TopNav />
                <div className="contact-wrapper">
                    <SideNav />
                    <div className="contact-sidebar mg-l-200">
                        <div className="contact-sidebar-header justify-content-between">
                            <h6 className="tx-uppercase tx-semibold mg-b-0">Inbox</h6>
                            <div className="dropdown tx-13">
                                <span className="tx-color-03">Sort:</span>
                                <Link to="/" className="dropdown-link link-02">Date</Link>
                                <div className="dropdown-menu dropdown-menu-left">
                                    <Link to="/" className="dropdown-item">Unread</Link>
                                    <Link to="/" className="dropdown-item">Pending</Link>
                                    <Link to="/" className="dropdown-item">Rejected</Link>
                                    <Link to="/" className="dropdown-item">Accepted</Link>
                                </div>
                            </div>
                        </div>
                        <div className="contact-sidebar-body">
                            <ul className="list-unstyled media-list mg-t-1 mg-b-0">
                                {
                                    requests.map((data, key) => {
                                        return (
                                            <li key={key} className={`media px-1 ${data._id === req_id ? "selected" : ""}`}>
                                                <div className="avatar avatar-sm"><span className="avatar-initial rounded-circle">{data.first_name[0]}</span></div>
                                                <div className="media-body px-1">
                                                    <div className="tx-color-03 d-flex align-items-center justify-content-between mg-b-0">
                                                        <span className="tx-12">{data.first_name + " " + data.last_name}</span>
                                                        <span className="tx-11">asd</span>
                                                    </div>
                                                    <h6 className="tx-13">New Chef Request</h6>
                                                    <p className="tx-12 tx-color-03 mg-b-1">
                                                        You got a new request from {data.first_name + " " + data.last_name} located at {data.postal_code}
                                                    </p>
                                                </div>
                                            </li>
                                        )
                                    })
                                }
                            </ul>
                        </div>
                    </div>
                    <div className="contact-content mg-l-200">
                        <div className="contact-content-header">
                            <div className="media">
                                <div className="avatar avatar-sm">
                                    <span className="avatar-initial rounded-circle">{current_restaurant.first_name}</span>
                                </div>
                                <div className="media-body mg-l-10">
                                    <h6 className="mg-b-2 tx-13">{current_restaurant.first_name + " " + current_restaurant.last_name}</h6>
                                    <span className="d-block tx-11 tx-color-03">Today, 11:40am</span>
                                </div>
                            </div>
                            <nav className="nav nav-icon-only mg-l-auto">
                                <Link to="#" data-toggle="tooltip" title="" className="nav-link d-none d-sm-block" data-original-title="Mark Unread">
                                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" className="feather feather-mail"><path d="M4 4h16c1.1 0 2 .9 2 2v12c0 1.1-.9 2-2 2H4c-1.1 0-2-.9-2-2V6c0-1.1.9-2 2-2z"></path><polyline points="22,6 12,13 2,6"></polyline></svg>
                                </Link>
                                <Link to="#" data-toggle="tooltip" title="" className="nav-link d-none d-sm-block" data-original-title="Add Tag">
                                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" className="feather feather-tag"><path d="M20.59 13.41l-7.17 7.17a2 2 0 0 1-2.83 0L2 12V2h10l8.59 8.59a2 2 0 0 1 0 2.82z"></path><line x1="7" y1="7" x2="7" y2="7"></line></svg>
                                </Link>
                                <Link to="#" data-toggle="tooltip" title="" className="nav-link d-none d-sm-block" data-original-title="Mark Important"><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" className="feather feather-star"><polygon points="12 2 15.09 8.26 22 9.27 17 14.14 18.18 21.02 12 17.77 5.82 21.02 7 14.14 2 9.27 8.91 8.26 12 2"></polygon></svg></Link>
                                <Link to="#" data-toggle="tooltip" title="" className="nav-link d-none d-sm-block" data-original-title="Trash"><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" className="feather feather-trash"><polyline points="3 6 5 6 21 6"></polyline><path d="M19 6v14a2 2 0 0 1-2 2H7a2 2 0 0 1-2-2V6m3 0V4a2 2 0 0 1 2-2h4a2 2 0 0 1 2 2v2"></path></svg></Link>
                                <Link to="#" data-toggle="tooltip" title="" className="nav-link d-none d-sm-block" data-original-title="Print"><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" className="feather feather-printer"><polyline points="6 9 6 2 18 2 18 9"></polyline><path d="M6 18H4a2 2 0 0 1-2-2v-5a2 2 0 0 1 2-2h16a2 2 0 0 1 2 2v5a2 2 0 0 1-2 2h-2"></path><rect x="6" y="14" width="12" height="8"></rect></svg></Link>
                            </nav>

                        </div>
                        <div className="contact-content-body ps ps--active-y">
                            <div className="pd-20 pd-lg-25 pd-xl-30">
                                <h5 className="mg-b-30">New Restaurant Request</h5>
                                <h6 className="tx-semibold mg-b-0">{current_restaurant.first_name + " " + current_restaurant.last_name}</h6>
                                <p>Greetings!</p>
                                <p>
                                    {"Phone number: " + current_restaurant.phone}
                                </p>
                                <p>{"Postal Code: " + current_restaurant.postal_code}</p>
                                <p>{"Email id: " + current_restaurant.email}</p>
                            </div>
                        </div>
                    </div>
                </div>
            </>
        );
    }
}
