import Tabs from "../../helper/Tabs";
import React, { Component } from "react";
import SideNav from "../components/SideNav";
import TopNav from "../components/TopNav";
import RestaurantDetailView from "../components/restaurant/read/RestaurantDetailView";
import RestaurantImageView from '../components/restaurant/read/RestaurantImageView';
import ViewPlan from '../components/restaurant/read/ViewPlan'
import ViewBankDetails from '../components/restaurant/read/ViewBankDetails'
import MealsView from '../components/restaurant/read/MealsView'
import axios from 'axios';

export default class ViewRestaurant extends Component {
    constructor(props) {
        super(props);
        this.state = {
            header: ["Details", "Images", "Meals", "Meal Plan", "Bank Details"],
        }
    }
    componentDidMount() {
        let url = window.location.href;
        const idofclient = url.split('id=')[1];
        axios.get('/api/newrest/' + idofclient).then(res => {
            this.setState({ restaurant: res.data, isupdate: true })
            this.components = [<RestaurantDetailView {...this.state.restaurant} />, <RestaurantImageView {...this.state.restaurant} />, <MealsView {...this.state.restaurant} />, <ViewPlan {...this.state.restaurant} />, <ViewBankDetails {...this.state.restaurant}/> ]
        }).catch(err => console.error(err))
    }
    components = []
    render() {
        const { header } = this.state
        return (
            <>
                <TopNav />
                <div className="contact-wrapper">
                    <SideNav />
                    <div className="contact-content" style={{ height: 640, marginBottom: 100, overflowY: 'scroll' }} >
                        <div
                            className="tab-content"
                            style={{ position: "absolute", left: 0, width: "80%" }}
                        >
                            <div id="contactInformation" className=" pd-20 pd-xl-25">
                                <Tabs>
                                    {
                                        header.map((data, i) => {
                                            return (
                                                <div key={i} className="tab-pane fade show active" label={data}>
                                                    {this.components[i]}
                                                </div>
                                            )
                                        })
                                    }
                                </Tabs>
                            </div>
                        </div>
                    </div>
                </div>
            </>
        );
    }
}
