import React, { Component } from "react";
import "jquery/dist/jquery.min.js";
import "datatables.net-dt/js/dataTables.dataTables";
import "datatables.net-dt/css/jquery.dataTables.min.css";
import "datatables.net-buttons-dt";
import "datatables.net-buttons/js/buttons.html5.js";
import "datatables.net-buttons-dt/css/buttons.dataTables.css";
import "datatables.net-select-dt";
import $ from "jquery";
import { Edit3, Eye, Trash2 } from "react-feather";
import { getRestaurants, deleteRestaurant } from "../../../../controllers/RestaurantController";
import { Modal, Button } from 'react-bootstrap';

const Clt = getRestaurants()
export default class DummyRestaurantTable extends Component {
  state = {
    showDeleteModal: false,
    currentindex: '',
    viewPage: false,
    editPage: false,
    tableData: [],
    isLoaded: false
  };

  componentDidMount() {
    const self = this;
    $("#clientResponsive tfoot th").each(function () {
      var title = $(this).text();
      $(this).html('<input type="text" placeholder="Search ' + title + '" />');
    });
    Clt.then((data) => {
      self.setState({ isLoaded: true, tableData: data });
      $("#clientResponsive").DataTable({
        paging: true,
        dom: "Bfrtip",
        responsive: true,
        buttons: [],
        initComplete: function () {
          this.api()
            .columns()
            .every(function () {
              var that = this;
              return $("input", this.footer()).on(
                "keyup change clear",
                function () {
                  if (that.search() !== this.value) {
                    that.search(this.value).draw();
                  }
                }
              );
            });
        },
      });
    })
      .catch((err) => {
        console.log(err);
      });
  }

  handleClose = () => {
    this.setState({ showDeleteModal: false })
  }
  handleDelete = () => {
    deleteRestaurant(this.state.currentindex)
    window.location.reload()
  }
  render() {
    const { tableData, showDeleteModal, isLoaded } = this.state;
    console.log(tableData);
    if (this.state.viewPage === true) {
      window.location.href = '/view-restaurant?id=' + this.state.currentindex
    }
    else if (this.state.editPage === true) {
      window.location.href = '/edit-restaurant?id=' + this.state.currentindex
    }
    else if (isLoaded) {
      return (
        <>
          <table id="clientResponsive" className=" table">
            <thead>
              <tr>
                <th
                  style={{
                    overflow: "hidden",
                    textOverflow: "ellipsis",
                    whiteSpace: "nowrap",
                    padding: 4,
                  }}
                >
                  Restaurant Name
              </th>
                <th
                  style={{
                    overflow: "hidden",
                    textOverflow: "ellipsis",
                    whiteSpace: "nowrap",
                    padding: 4,
                  }}
                >
                  Location
              </th>
                <th
                  style={{
                    overflow: "hidden",
                    textOverflow: "ellipsis",
                    whiteSpace: "nowrap",
                    padding: 4,
                  }}
                >
                  Email
              </th>
                <th
                  style={{
                    overflow: "hidden",
                    textOverflow: "ellipsis",
                    whiteSpace: "nowrap",
                    padding: 4,
                  }}
                >
                  Status
              </th>
                <th
                  style={{
                    overflow: "hidden",
                    textOverflow: "ellipsis",
                    whiteSpace: "nowrap",
                    padding: 4,
                  }}
                >
                  Actions
              </th>
              </tr>
            </thead>
            <tbody>
              {tableData.map((data, key) => {
                return (
                  <tr key={key}>
                    <td
                      style={{
                        overflow: "hidden",
                        textOverflow: "ellipsis",
                        whiteSpace: "nowrap",
                        padding: 4,
                      }}
                    >
                      {data.restaurant_name}
                    </td>
                    <td
                      style={{
                        overflow: "hidden",
                        textOverflow: "ellipsis",
                        whiteSpace: "nowrap",
                        padding: 4,
                      }}
                    >
                      {data.locality + ', ' + data.city + ', ' + data.state + ', ' + data.country + ', ' + data.postal_code}
                    </td>
                    <td
                      style={{
                        overflow: "hidden",
                        textOverflow: "ellipsis",
                        whiteSpace: "nowrap",
                        padding: 4,
                      }}
                    >
                      {data.email}
                    </td>

                    <td
                      style={{
                        overflow: "hidden",
                        textOverflow: "ellipsis",
                        whiteSpace: "nowrap",
                        padding: 4,
                        color: "green",
                      }}
                    >
                      {data.status || "Active"}
                    </td>
                    <td
                      style={{
                        overflow: "hidden",
                        textOverflow: "ellipsis",
                        whiteSpace: "nowrap",
                        padding: 4,
                      }}
                    >
                      <Eye onClick={() => { this.setState({ viewPage: true, currentindex: data._id }) }} size={18} />
                      <Edit3 onClick={() => { this.setState({ editPage: true, currentindex: data._id }) }} size={18} />
                      <Trash2 onClick={() => { this.setState({ showDeleteModal: true, currentindex: data._id }) }} size={18} />

                    </td>
                  </tr>
                );
              })}
            </tbody>
            <tfoot>
              <tr>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
              </tr>
            </tfoot>
          </table>

          <Modal
            show={showDeleteModal}
            onHide={this.handleClose}
            keyboard={false}
          >
            <Modal.Header closeButton>
              <Modal.Title>Modal title</Modal.Title>
            </Modal.Header>
            <Modal.Body>
              Are you sure you want to delete? Action is irreversible!!!
        </Modal.Body>
            <Modal.Footer>
              <Button variant="secondary" onClick={this.handleClose}>
                Close
          </Button>
              <Button onClick={this.handleDelete} variant="danger">Delete</Button>
            </Modal.Footer>
          </Modal>
        </>
      );
    }
    else {
      return (
        <div>Loading...</div>
      )
    }
  }
}
