import React, { Component } from 'react'

export default class RestaurantDetailForm extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...this.props,
        }
    }
    render() {
        return (
            <>
                <div className="row row-sm px-1">
                    <div className="col-6 col-sm-4 ">
                        <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                            Restaurant Name
                    </label>
                        <p className="mg-b-0">
                            <input
                                disabled
                                name="restaurant_name"
                                value={this.state.restaurant_name}

                            />
                        </p>
                    </div>
                    <div className="col-6 col-sm-4 ">
                        <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                            Phone Number
                    </label>
                        <p className="mg-b-0">
                            <input
                                disabled
                                name="phone"
                                value={this.state.phone}

                            />
                        </p>
                    </div>
                    <div className="col-6 col-sm-4 ">
                        <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                            Email Address
                    </label>
                        <p className=" mg-b-0">
                            <input
                                disabled
                                name="email"
                                value={this.state.email}

                            />
                        </p>
                    </div>


                </div>
                <div className="row row-sm px-1">
                    <div className="col-6 col-sm-4">
                        <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                            Owner Name
                    </label>
                        <p className="mg-b-0">
                            <input
                                disabled
                                name="owner_name"
                                value={this.state.owner_name}

                            />
                        </p>
                    </div>
                    <div className="col-6 col-sm-4">
                        <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                            Street
                    </label>
                        <p className="mg-b-0">
                            <input
                                disabled
                                name="locality"
                                value={this.state.locality}

                            />
                        </p>
                    </div>
                    <div className="col-6 col-sm-4">
                        <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                            City
                    </label>
                        <p className="mg-b-0">
                            <input
                                disabled
                                name="city"
                                value={this.state.city}

                            />
                        </p>
                    </div>
                </div>
                <div className="row row-sm px-1">
                    <div className="col-6 col-sm-4">
                        <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                            State
                    </label>
                        <p className="mg-b-0">
                            <input
                                disabled
                                name="state"
                                value={this.state.state}

                            />
                        </p>
                    </div>
                    <div className="col-6 col-sm-4">
                        <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                            Country
                    </label>
                        <p className="mg-b-0">
                            <input
                                disabled
                                name="country"
                                value={this.state.country}

                            />
                        </p>
                    </div>
                    <div className="col-6 col-sm-4">
                        <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                            Postal Code
                    </label>
                        <p className="mg-b-0">
                            <input
                                disabled
                                name="postal_code"
                                value={this.state.postal_code}

                            />
                        </p>
                    </div>

                    <div className="col-6 col-sm-4 ">
                        <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                            Cuisine Type
                    </label>
                        <p className="mg-b-0">
                            <input
                                disabled
                                name="cuisine_type"
                                value={this.state.cuisine_type}

                            />
                        </p>
                    </div>

                    <div className="col-6 col-sm-4 ">
                        <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                            Commission
                    </label>
                        <p className="mg-b-0">
                            <input
                                disabled
                                name="commision"
                                value={this.state.commission}

                            />
                        </p>
                    </div>


                    <div className="col-6 col-sm-4 ">
                        <div className="col-6 col-sm-4">
                            <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10 ml-n3">
                                Status
                      </label>
                            <p className="mg-b-0">
                                <input
                                    disabled
                                    name="status"
                                    value={this.state.status}

                                />
                            </p>

                        </div>
                    </div>
                </div>
                <div className="row row-sm px-1  mg-t-20">
                    <div className="col-6 col-sm  mg-t-10">
                        <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                            About
                    </label>
                        <p className="mg-b-0 px-1">
                            <textarea
                                className="col-sm mg-t-10"
                                disabled
                                name="about"
                                value={this.state.about}
                                maxLength={400}
                            />
                        </p>
                    </div>
                </div>
                {/* row */}
            </>
        )
    }
}
