import React, { Component } from 'react'

export default class ViewPlan extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...this.props.plan
        }
    }
    render() {
        const { twoPlan, fifteenPlan, thirtyPlan } = this.state
        return (
            <>

                <div className="d-flex align-items-center justify-content-between mg-t-25 mx-1 mg-b-25">
                    <h6 className="tx-12  mg-b-0">2 Day Meal Plan</h6>
                </div>
                <div className="row row-sm mx-1">
                    <div className="col-6 col-sm-6 ">
                        <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                            Base Price
                    </label>
                        <p className="mg-b-0">
                            <input
                                name="base_2price"
                                value={twoPlan.base_2price || ""}
                                disabled
                            />
                        </p>
                    </div>
                    <div className="col-6 col-sm-6 ">
                        <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                            Customer Price
                    </label>
                        <p className="mg-b-0">
                            <input
                                disabled
                                name="customer_2price"
                                value={twoPlan.customer_2price || ""}
                            />
                        </p>
                    </div>
                </div>

                <div className="d-flex align-items-center justify-content-between mg-t-25 mx-1 mg-b-25">
                    <h6 className="tx-12  mg-b-0">15 Day Meal Plan</h6>
                </div>
                <div className="row row-sm mx-1">
                    <div className="col-sm-6">
                        <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                            Base Price
                    </label>
                        <p className="mg-b-0">
                            <input
                                disabled
                                name="base_15price"
                                value={fifteenPlan.base_15price || ""}
                            />
                        </p>
                    </div>
                    <div className="col-sm-6">
                        <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                            Customer Price
                    </label>
                        <p className=" mg-b-0">
                            <input
                                disabled
                                name="customer_15price"
                                value={fifteenPlan.customer_15price || ""}
                            />
                        </p>
                    </div>
                </div>

                <div className="d-flex align-items-center justify-content-between mg-t-25 mx-1 mg-b-25">
                    <h6 className="tx-12  mg-b-0">30 Day Meal Plan</h6>
                </div>
                <div className="row row-sm mx-1">
                    <div className="col-sm-6">
                        <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                            Base Price
                    </label>
                        <p className="mg-b-0">
                            <input
                                disabled
                                name="base_30price"
                                value={thirtyPlan.base_30price || ""}
                            />
                        </p>
                    </div>
                    <div className="col-sm-6">
                        <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                            Customer Price
                    </label>
                        <p className="mg-b-0">
                            <input
                                disabled
                                name="customer_30price"
                                value={thirtyPlan.customer_30price || ""}
                            />
                        </p>
                    </div>
                </div>

            </>

        )
    }
}
