import React, { Component } from 'react'
import { imageConverter } from '../../../../helper/imageConverter'
import DynamicImages from './DynamicImages';
import { Toast } from 'react-bootstrap'
export default class ImageTabDetails extends Component {
    constructor(props) {
        super(props);
        this.state = {
            // ...this.props,
            imgLabel: "Choose File",
            image: null,
            documents: []
        }
    }

    onChangeFile = (e) => {
        const name = e.target.name
        imageConverter(e.target.files[0], name).then(res => {
            let documents = this.state.documents;
            documents.push(res)
            this.setState({ documents: documents });
        }).catch((err) => {
            console.error(err)
        })
        this.props.getImages({ ...this.state })

    }
    getImages = (data) => {
        this.props.getImages({ ...this.state })
        this.setState({ documents: data })
    }
    setData = () => {
        this.setState({ showToast: true })
        this.props.getImages({ ...this.state })
    }
    render() {
        const { showToast } = this.state
        return (
            <>
                <div className="d-flex align-items-center justify-content-between mx-2">
                    <h6 className="tx-12  mg-b-0">Documents</h6>
                    <button className="btn btn-link" type="button" onClick={this.setData}>Save</button>
                </div>
                <div className="row row-sm mg-t-10 mx-1">

                    <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                        Restaurant image
                    </label>
                    <div className="custom-file px-2">
                        <input
                            type="file"
                            className="custom-file-input"
                            accept=".png, .jpg, .webp, .gif"
                            name="restaurant_image"
                            id="customFilen"
                            onChange={this.onChangeFile}
                        />
                        <label className="custom-file-label" htmlFor="customFile">
                            {this.state.imgLabel}
                        </label>
                    </div>
                </div>

                <div className="row row-sm mg-t-10 mx-1">
                    <label className="tx-10 tx-medium tx-spacing-1 tx-color-03 tx-uppercase tx-sans mg-b-10">
                        Banner
                  </label>
                    <div className="custom-file px-2">
                        <input
                            type="file"
                            className="custom-file-input"
                            id="customFile1"
                            accept=".png, .jpg, .webp, .gif"
                            name="Banner"
                            onChange={this.onChangeFile}

                        />
                        <label className="custom-file-label" htmlFor="customFile1">
                            {this.state.imgLabel}
                        </label>
                    </div>
                </div>
                <DynamicImages {...this.state} getImages={this.getImages} />
                <Toast style={{ position: 'absolute', top: 0, right: 0, borderColor: '#44cf33' }}
                    onClose={() => { this.setState({ showToast: false }); }}
                    show={showToast}
                    delay={2000}
                    autohide>
                    <Toast.Body>
                        Saved!!!
                    </Toast.Body>
                </Toast>
            </>
        )
    }
}
